<template>
  <div class="right-contain">
    <div class="right-area" v-if="!copyDatas.isArea">
      <div class="content-detail">
        <span class="title">标记点名称</span>
        <div class="dott-records">
          <el-input v-model="copyDatas.label" disabled></el-input>
          <!-- <el-select size="small" v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
          </el-select>-->
        </div>
      </div>
      <div class="content-detail">
        <span class="title">Positon</span>
        <div class="text-content">
          <div class="list">
            <div>top</div>
            <input v-model="copyDatas.top" disabled />
          </div>
          <div class="list">
            <div>left</div>
            <input v-model="copyDatas.left" disabled />
          </div>
        </div>
      </div>
      <!-- <div class="content-detail">
      <span class="title">Size</span>
      <div class="text-content">
        <div class="list">
          <div>width</div>
          <input :placeholder="detailData.width" />
        </div>
        <div class="list">
          <div>height</div>
          <input :placeholder="detailData.height" />
        </div>
      </div>
      </div>-->
      <!-- 操作图标 添加 -->
      <!-- <div class="operate-icon">
      <img src="../assets/addicon.png" alt @click="addModelBtn" />
      <img :src="srcDelete" alt @click="deleteModelBtn" />
      </div>-->
      <!-- <div class="content-detail">
      <span class="title">备注</span>
      <div class="textarea-content">
        <textarea />
      </div>
      </div>-->
      <div class="content-detail">
        <span class="title">图片详情</span>
        <div class="img-content">
          <el-upload
            action
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :http-request="handleRequest"
            :file-list="fileList"
            :disabled="!disable"
            @click.native="clickUploadBtn"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt />
          </el-dialog>
        </div>
      </div>
      <!-- <span @click="showHelp = !showHelp" style="{color:red}">帮助</span> -->
      <div class="content-detail descript" v-if="showHelp">
        1.如何使用区域画图？
        <div>
          （1）右键，点击
          <b style="color: red">区域开始</b>，此时出现一个蓝点表示画图开始。
        </div>
        <div>
          （2）之后绘图，绘图完成需要在
          <span style="fontsize: 20px; fontweight: 600">该区域</span>右键点击
          <b style="color: red">区域完成</b>
        </div>
        <div style="margintop: 20px">
          （3）修改区域，
          <b style="color: red">双击该区域</b>，此时区域处在
          <span style="fontsize: 20px; fontweight: 600">(活跃状态=区域出现红色的边框)</span>，可点击开始绘图，切记 切记 切记操作完成后都需要右键点击
          <b style="color: red">区域完成</b>。
        </div>
      </div>
    </div>
    <div class="right-area" v-else>
      <div class="content-detail">
        <span class="title">图片详情</span>
        <div class="img-content">
          <el-upload
            action
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :http-request="handleRequest"
            :file-list="fileList"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt />
          </el-dialog>
        </div>
      </div>
      <!-- <span @click="showHelp = !showHelp" style="{color:red}">帮助</span> -->
      <div class="content-detail descript" v-if="showHelp">
        1.如何使用区域画图？
        <div>
          （1）右键，点击
          <b style="color: red">区域开始</b>，此时出现一个蓝点表示画图开始。
        </div>
        <div>
          （2）之后绘图，绘图完成需要在
          <span style="fontsize: 20px; fontweight: 600">该区域</span>右键点击
          <b style="color: red">区域完成</b>
        </div>
        <div style="margintop: 20px">
          （3）修改区域，
          <b style="color: red">双击该区域</b>，此时区域处在
          <span style="fontsize: 20px; fontweight: 600">(活跃状态=区域出现红色的边框)</span>，可点击开始绘图，切记 切记 切记操作完成后都需要右键点击
          <b style="color: red">区域完成</b>。
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import eventbus from "../utils/evenbus";
let img = require("../assets/delete.png");
let initimg = require("../assets/delete1.png");

import { uploadImg, deleteImg } from "@/network/homes"


export default {
  name: "rightArea",
  data() {
    return {
      fileList: [],
      dotNames: "厨房",
      showHelp: false,
      srcDelete: initimg,
      dialogImageUrl: "",
      dialogVisible: false,
      value: "",
      copyDatas: {}
    };
  },
  computed:{
      disable(){
        let isshow=this.$store.state.userInfo.edit==1?true:false;
        return  isshow
      }
  },
  props: {
    detailData: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  watch: {
    detailData: {
      handler(obj) {
        obj.isopenDel == true
          ? (this.srcDelete = img)
          : (this.srcDelete = initimg);
        let transitionArr = JSON.parse(JSON.stringify(obj));
        transitionArr.left = Number(Number(transitionArr.left).toFixed(4))
        transitionArr.top = Number(Number(transitionArr.top).toFixed(4))
        this.copyDatas = transitionArr;
        // 还原已有标记图片 
        this.fileList = this.copyDatas?.picUrl?.map(val => {
          return { url: val }
        })

      }
    },
    copyDatas: {
      handler(newVal) {
        console.log(newVal);
        // 发送监听 
        eventbus.$emit("sendInfoData", newVal);
      },
      deep: true
    },
    fileList: {
      handler(newVal) {
        console.log(newVal)
        // 发送监听 
        this.copyDatas.picUrl = newVal?.map(val => {
          return val.url;
        })
      },
      deep: true
    }
  },
  methods: {
    clickUploadBtn(){
     if(!this.disable)return this.$message.error("您无权限上传!")
    },
    handleRequest(param) {
      let fileObj = param.file;
      let _that = this;
      var reader = new FileReader();
      console.log(fileObj);
      reader.readAsDataURL(fileObj);
      reader.addEventListener(
        "load",
        function() {
          console.log(reader.result);
          let obj = {
            fileName: fileObj.name,
            fileContext: reader.result,
          };
          uploadImg(obj).then((da) => {
            let datas = da.data;
            if (datas.errcode == 0) {
              _that.copyDatas.picUrl.push(datas.data);
              _that.$message.success("上传成功！")
            } else {
              _that.$message.error(
                "数据获取失败" + JSON.stringify(da) + "请重试上传！"
              );
            }
          });
        },
        false
      );

    },
    handleChange(file, fileList) {
    },
    handleRemove(file, fileList) {
      let url = file.url;
      let index = this.fileList.findIndex(val => {
        return val === url;
      })
      // 删除图片
      deleteImg({ fileName: url }).then(da => {
        if (da.data.errcode == 0) {
          this.$message.success("上传成功！")
          // 删除 filelist
          this.fileList.splice(index, 1)
        } else {
          this.$message.error(
            "数据删除失败" + JSON.stringify(da) + "请重试！"
          );
        }
      }).catch(e => {
        this.$message.error(
          "数据删除失败" + JSON.stringify(e) + "请重试！"
        );
      })


    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    addModelBtn() {
      eventbus.$emit("addModel");
    },
    deleteModelBtn() {
      if (this.srcDelete == img) {
        eventbus.$emit("pleaseDelModel");
      }
    },
  },
  beforeDestroy() {
    eventbus.$off();
  }
};
</script>

<style  lang="scss">
.right-contain {
  height: 100%;
  .right-area {
    padding: 30px 10px 0;
    text-align: left;
    position: relative;
    height: 100%;
    box-sizing: border-box;
    .title {
      font-weight: 600;
    }
    .content-detail {
      padding-bottom: 26px;
      .dott-records {
        margin-top: 10px;
      }
      .text-content {
        margin-top: 10px;
        display: flex;
        font-size: 11px;
        color: #bbb;
        font-weight: 300;
        div.list {
          width: 50%;
          padding-left: 5px;
        }
        input {
          width: 80px;
          display: inline-block;
          border: 1px solid #bfbfca;
          margin-top: 2px;
          height: 16px;
          margin: 2px 14px 0 4px;
        }
      }
      .titles {
        margin: 0 5px;
        color: #999;
        font-size: 14px;
      }
      .textarea-content {
        padding: 10px 10px 0;
        textarea {
          width: 190px;
          border: 1px solid #bfbfca;
        }
      }
      .img-content {
        overflow: hidden;
        height: 300px;
        // border: 1px solid red;
        overflow-y: scroll;
        padding: 12px;
        &::-webkit-scrollbar {
          // 滚动条的背景
          width: 16px;
          background: #fff;
          height: 14px;
        }

        &::-webkit-scrollbar-track,
        &::-webkit-scrollbar-thumb {
          border-radius: 999px;
          width: 20px;
          border: 5px solid transparent;
        }

        &::-webkit-scrollbar-track {
          box-shadow: 1px 1px 5px #fff;
        }

        &::-webkit-scrollbar-thumb {
          //滚动条的滑块样式修改
          width: 20px;
          min-height: 20px;
          background-clip: content-box;
          box-shadow: 0 0 0 5px #999 inset;
        }
        .el-upload {
          width: 80px;
          height: 80px;
          position: relative;
          i {
            width: 30px;
            height: 30px;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            margin: auto;
          }
        }
        .el-upload-list {
          li {
            width: 80px;
            height: 80px;
          }
        }
      }
    }
    .operate-icon {
      text-align: center;
      position: absolute;
      bottom: 20px;
      left: 0;
      right: 0;
      img {
        padding-left: 15px;
        width: 20px;
      }
    }
    .descript {
      height: 260px;
      overflow-y: scroll;
      &::-webkit-scrollbar {
        // 滚动条的背景
        width: 16px;
        background: #fff;
        height: 14px;
      }

      &::-webkit-scrollbar-track,
      &::-webkit-scrollbar-thumb {
        border-radius: 999px;
        width: 20px;
        border: 5px solid transparent;
      }

      &::-webkit-scrollbar-track {
        box-shadow: 1px 1px 5px #fff;
      }

      &::-webkit-scrollbar-thumb {
        //滚动条的滑块样式修改
        width: 20px;
        min-height: 20px;
        background-clip: content-box;
        box-shadow: 0 0 0 5px #999 inset;
      }
    }
  }
}
</style>
