import Vue from "vue";
import request from "./axios"
import store from "./../store"

let {
    tzid,
    mdid,
    djid
} = store.state.userInfo;

// 合同分类11
let baseurl;
if (process.env.NODE_ENV == "development") {
    baseurl = "/dev"
} else {
    baseurl = "http://192.168.35.96:8900/svr-erpcore"

}
export function getJmbtInfo(djid) {
    let json = {
        tzid,
        djid
    }
    return request.post(baseurl + "/businessPlanning/getJmbtInfo", json);
}


export function setJmbtInfo(obj) {
    let json = {
        "id": djid,
        tzid,
        mdid,
        "BaseMap": [
            "static/img/ee.jpg"
        ],
        content: obj.content

    }
    return request.post(baseurl + "/businessPlanning/setJmbtInfo", json);
}

// upload img
export function uploadImg(obj) {
    return request.post(baseurl+"/file/upload", obj);
}

export function deleteImg(obj) {
    return request.post(baseurl+"/file/remove", obj);
}