<template>
  <div
    class="draggable-container"
    ref="parentRef"
    @contextmenu="contextMenuBtn"
    @click="parengClickBtn"
    :style="{ width: imgW }"
  >
    <!-- 背景图片 -->
    <img :src="backImg" alt @mousedown="mouseDownBtn" ref="imgRef" @load="imgload" />
    <div class="polon-beign">loremoo</div>
    <!-- 区域容器-->
    <svg
      id="svg"
      ref="svg"
      :width="nowWidth"
      :height="nowHeight"
      @mousedown="mouseDownBtn"
      @click="svgclick"
      xmlns="http://www.w3.org/2000/svg"
      version="1.1"
    >
      <!-- <defs>
        <filter id="f1" x="0" y="0">
          <feGaussianBlur in="SourceGraphic" stdDeviation="3" />
        </filter>
      </defs>-->
      <circle
        :cx="cx"
        :cy="cy"
        r="5"
        stroke="#FFF"
        stroke-width="3"
        fill="#409EFF"
        v-if="isshowBPlo"
      />

      <template v-for="(val, index) in areasDatas">
        <polygon
          @contextmenu="areacontextMenuBtns"
          :key="index"
          :points="val.points"
          :style="activeIndex == index ? hasAreasColors[index].hborder : hasAreasColors[index].nborder
          "
          @dblclick.stop="polydlBtns(val, index)"
        >
          <!-- <div style="position: absolute; top: 0">lorem</div> -->
        </polygon>
      </template>
      <!-- <polygon
        points=" 100,0 300,410 170,450 "
        style="fill: lime; stroke: purple; stroke-width: 0"
      />-->
      <template v-for="(val, index) in addTextDesArr">
        <text
          @contextmenu.stop="areacontextMenuBtns"
          :x="val.x"
          :y="val.y"
          fill="#000"
          font-size="22px"
          :key="index + 'i'"
        >{{ val.value }}</text>
      </template>
    </svg>
    <!-- 打点区域 -->
    <template v-for="(val, index) in dottedDatas">
      <VueDragResize
        :key="index"
        :w="val.width || 20"
        :h="val.height || 20"
        :x="val.left || 0"
        :y="val.top || 0"
        v-on:dragging="resize"
        :isDraggable="false"
        :isResizable="false"
        :parentW="nowWidth"
        :parentH="nowHeight"
        :parentLimitation="true"
        style="background: transport; border-radius: 50%"
        @clicked="clickModelBtn(index)"
        @dragstop="ondreopStop(index)"
        :isActive="isActive == index"
      >
        <img src="../assets/dibiao.png" alt />
        <!-- <div style="fontSize:20px">
          lorem
        </div>-->
      </VueDragResize>
    </template>

    <!-- 区域右键-->

    <ul class="ul-content" ref="areas" @contextmenu.stop="areacontextMenuBtns">
      <li @click="deleteAreaBtn">删除区域</li>
      <li @click="addAreaDescBtn">添加备注</li>
      <li @click="areaBackBtns">撤销上一步</li>
      <li @click="addAreaSucss">区域完成</li>
    </ul>

    <!-- 右键组件 -->
    <ul class="ul-content" ref="ul">
      <li class="title">打点</li>
      <li @click="deleteModelBtn">。删除打点</li>
      <li @click="addModelBtn">。标记节点</li>
      <li class="title">区域</li>
      <li @click="addAreaBtns">。区域开始</li>
    </ul>

    <el-dialog title="提示" :visible.sync="dialogVisible" width="40%" :close-on-click-modal="false">
      <div class="select-content">
        <span>选择点标记名称：</span>
        <div class="select-dottname">
          <el-select size="small" v-model="dottedvalue" placeholder="请选择">
            <el-option v-for="item in totalDottNames" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </div>
      </div>
      <div class="remind">
        <span class="star">*</span>如若已存在该点标记名称，则会覆盖之前 。
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmBtn">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import VueDragResize from "vue-drag-resize";
import eventbus from "../utils/evenbus";
import { getJmbtInfo } from "@/network/homes"
export default {
  name: "leftArea",
  props: {
    stateStr: {
      type: String,
      default: null,
    },
    stateTag: {
      type: Number,
      default: 0,
    },
    // dottedDetail: {
    //   type: Object,
    //   default() {
    //     return {}
    //   }
    // }
  },
  data() {
    return {
      dialogVisible: false,
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      // deleteIndexTotal:"",
      cx: null,
      cy: null,
      isshowBPlo: false,
      addTextDesArr: [
        // {
        //   x: 150,
        //   y: 230,
        // },
        // {
        //   x: 200,
        //   y: 100,
        // },
      ],
      hasAreasColors: [],
      // 区域随机颜色
      areaColors: [
        "rgb(197,149,149)",
        "rgb(115,154,136)",
        "rgb(164,195,240)",
      ],
      // 当前活跃的区域设置有边框
      activeIndex: null,
      // 当前活跃的区域
      activeArea: null,
      // 区域容器
      areas: [],
      //  sum 0,1,2表区域打点的开始，打点中，打点结束
      sum: 0,
      imgW: "760px",
      imgH: null,
      backImg: "",
      // backImg: "https://s9.rr.itc.cn/r/wapChange/201610_16_22/a74nkl7684685281855.jpg",
      nowWidth: null,
      nowHeight: null,
      isActive: -1,
      ul: null,
      longDottedSizeW: 2000,
      shortDottedSizeW: 200,
      // 打点容器
      addGraCoordinate: [
        // { top: 0, left: 0, width: 20, height: 20 },
        // { top: 100, left: 130, width: 20, height: 20 },
        // { top: 200, left: 230, width: 20, height: 20 },
        // { top: 300, left: 30, width: 20, height: 20 },
      ],
      // addAreas: [],
      dottedDetails: {},
      dottedDetail: {},
      totalDottNames: [],
      areasDatas: [],
      dottedDatas: [],
      dottedvalue: ""
    };
  },
  computed: {
    df() {
      return this.dottedDetail;
    }
  },
  created() {
    this.load = this.$Loading.service({
      fullscreen: true,
    });
    // 获取后台打点接口数据
    this.getHomeData(18010);
    // 是否 从 后台里面传进来已有的图片打点记录,有则初始化数据
    // let tagData =
    //   JSON.parse(window.localStorage.getItem("TAG_DATE_DETAIL")) || {};
    // if (Object.keys(tagData).length > 0) {
    //   let {
    //     addGraCoordinate,
    //     nowWidth,
    //     nowHeight,
    //     imgSrc,
    //     areaDetail,
    //   } = tagData;
    //   // this.backImg = imgSrc;
    //   // this.imgW = nowWidth + "px";
    //   // this.imgH = nowHeight + "px";
    //   this.addGraCoordinate = addGraCoordinate;

    //   // 本地存储区域回填
    //   let { hasAreasColors, addTextDesArr, areas } = areaDetail;
    //   this.hasAreasColors = hasAreasColors;
    //   this.addTextDesArr = addTextDesArr;
    //   this.areas = areas;
    // }
  },
  mounted() {
    // 监听右边区域删除打点model
    eventbus.$on("pleaseDelModel", () => {
      this.addGraCoordinate.splice(this.isActive, 1);
      this.isActive = -1;
      this.$emit("clickDetail", {
        width: 0,
        height: 0,
        left: 0,
        top: 0,
        isopenDel: false,
      });
    });

    // 添加打点按钮
    // eventbus.$on("addModel", () => {
    //   this.addGraCoordinate.push({
    //     left: 200,
    //     top: 200,
    //     width: 20,
    //     height: 20,
    //   });
    // });


    // 添加打点按钮
    eventbus.$on("sendInfoData", (data) => {
      console.log("----------");
      console.log(data);
      if (!data.isArea) {
        this.dottedDatas[data.indexState] = data;
      } else {
        this.areasDatas[data.indexState] = data;
      }
    });
  },

  components: {
    VueDragResize,
  },
  methods: {
    getHomeData(id) {
      getJmbtInfo(id).then(da => {
        if (da.data.errcode == 0) {
          this.load.close();
          // da.data.data = {
          //   "id": 18010,
          //   "tzid": 1,
          //   "mdid": 8798,
          //   "BaseMap": [
          //     "static/img/ee.jpg"
          //   ],
          //   "content": {
          //     "Point": [],
          //     "LinkString": [
          //       {
          //         "picUrl": [],
          //         "coordinates": [
          //           [
          //             12,
          //             455
          //           ],
          //           [
          //             52,
          //             445
          //           ]
          //         ],
          //         "label": "画面39",
          //         "properties": {}
          //       }
          //     ],
          //     "Polygon": []
          //   }
          // }
          this.dottedDetail = da.data.data;
          // 1.先底图进行回流
          this.backImg = this.dottedDetail.BaseMap[0];
          // 2.将所有点的标签名称进行汇总，因为之后只能在已有的标签名中进行添加
          let totalDottNames = this.dottedDetail.content.Point.map(val => {
            return val.label;
          })
          this.totalDottNames = totalDottNames;
          this.dottedvalue = this.totalDottNames[0]
          // 3.处理点记录数据 
          this.dottedDatas = this.dottedDetail.content.Point.map(val => {
            return {
              properties: val.properties,
              mxid: val.mxid,
              left: val.coordinates[0],
              top: val.coordinates[1],
              label: val.label,
              picUrl: val.picUrl
            }
          })
          // 一开始 给右边区域回流数据，默认显示第一个
          if (this.dottedDatas.length > 0) {
            setTimeout(val=>{
              this.clickModelBtn(0);
            },300)
            // console.log(this.dottedDatas[0]);
            // this.isActive = 0;
            // this.$emit("clickDetail", {
            //   indexState: 0,
            //   ...this.dottedDatas[0],
            // });
          }
          // 4.将区域数据回流,及随机 生成区域颜色
          this.areasDatas = this.dottedDetail.content.Polygon.map(val => {
            let arr = val.coordinates.map(val => {
              return `${val[0]},${val[1]}`
            })
            let obj = arr.join(" ");

            return {
              properties: val.properties,
              points: obj,
              label: val.label,
              picUrl: val.picUrl
            }
          })
          // 这边回流区域的时候，随机生成不同颜色的区域hasAreasColors=》array
          for (let i of this.areasDatas) {
            this.hasAreasColors.push(this.randomColors());
          }
        } else {
          this.$message.error("获取点信息失败！" + JSON.stringify(da.data.data) || "请重试！")
        }
      }).catch(e => {
        this.$message.error("获取点信息失败！" + e || "请重试！");
      })
    },
    confirmBtn() {
      if (this.totalDottNames.length == 0) return this.dialogVisible = false;
      let selectIndex = this.dottedDatas.findIndex(val => {
        return val.label === this.dottedvalue;
      })
      let properties = this.dottedDatas[selectIndex].properties;
      let mxid = this.dottedDatas[selectIndex].mxid;
      this.dottedDatas.splice(selectIndex, 1);
      if (selectIndex >= 0) {
        this.dottedDatas.push({
          properties,
          mxid,
          left: this.offsetX,
          top: this.offsetY,
          label: this.dottedvalue,
          picUrl: [],
        });


        this.isActive = this.dottedDatas.length - 1;
        this.$emit("clickDetail", {
          indexState: this.isActive,
          ...this.dottedDatas[this.dottedDatas.length - 1],
          isopenDel: true,
        });
        this.$refs.ul.style.display = "none";
      }


      // 关闭dialog
      this.dialogVisible = false;
    },
    handleClose(done) {
      this.dialogVisible = false;
      // this.$confirm('确认关闭？')
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => { });
    },
    // 添加区域说明
    addAreaDescBtn() {
      this.$prompt("请区域备注", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
      })
        .then(({ value }) => {
          let copyarr = this.activeArea.points.split(" ");
          let topAve = [];
          let leftAve = [];
          copyarr.map((val) => {
            let leftaverage = val.split(",")[0];
            let topaverage = val.split(",")[1];
            leftAve.push(leftaverage);
            topAve.push(topaverage);
          });
          let leftmin =
            (
              (Math.max(...leftAve) - Math.min(...leftAve)) / 2 +
              Math.min(...leftAve)
            ).toFixed(2) - 30;
          let topmin = (
            (Math.max(...topAve) - Math.min(...topAve)) / 2 +
            Math.min(...topAve)
          ).toFixed(2);
          //
          // let gg = this.addTextDesArr.find((val) => {
          //   return val.stateIndex == this.activeIndex;
          // });
          // let exist=this.addTextDesArr[this.activeIndex];
          // if(exist){
          this.addTextDesArr.splice(this.activeIndex, 1, {
            stateIndex: this.activeIndex,
            x: leftmin,
            y: topmin,
            value,
          });
          // }
          // if (gg) {
          //   gg.value = value;
          //   gg.x = leftmin;
          //   gg.y = topmin;
          // } else {
          //   this.addTextDesArr.splice(this.activeIndex, 1, {
          //     stateIndex: this.activeIndex,
          //     x: leftmin,
          //     y: topmin,
          //     value,
          //   });
          // }

          // this.$message({
          //   type: 'success',
          //   message: '你的备注是: ' + value
          // });
        })
        .catch(() => {
          // this.$message({
          //   type: "info",
          //   message: "取消输入",
          // });
        });
    },

    // 随机生成颜色
    randomColors() {
      let g = this.areaColors[Math.round(Math.random() * 2)];
      let active = "stroke:red;";
      // console.log(this.areaColors[Math.round(Math.random() * 3)]);
      let hborder = active + "fill:" + g + ";stroke-width:3";
      let nborder = active + "fill:" + g + ";stroke-width:0";
      return {
        hborder,
        nborder,
      };
    },
    areacontextMenuBtns(e) {
      //
      this.$refs.ul.style.display = "none";
      if (this.sum != 1) return e.preventDefault();
      e.preventDefault();
      this.$refs.areas.style.left = e.clientX + "px";
      this.$refs.areas.style.top = e.clientY + "px";
      this.$refs.areas.style.display = "block";
    },
    // 双击区域表示持出先选中
    polydlBtns(selectVal, index) {
      // 如果有选中点，就失去焦点，-1代表失去焦点
      this.isActive = -1;
      if (this.sum == 1) return;
      // selectVal.style = "fill:lime;stroke:rgb(128, 73, 0);stroke-width:1";
      // 设置为区域绘画过程中
      this.sum = 1;
      //选中当前所活跃的
      console.log("双击出现点击的对象", selectVal);
      this.activeArea = selectVal;
      // 活跃区域的下标
      this.activeIndex = index;


      // 将区域数据进行回填右栏目
      this.$emit("clickDetail", {
        indexState: index,
        // 设置一个属性 辨认是回流区域数据
        isArea: true,
        ...this.areasDatas[this.activeIndex],
        isopenDel: true,
      });

    },
    // 区域单击表示闭环
    polydlBtn() {
      // this.setTime = setTimeout((val) => {
      if (this.sum == -1) return;
      if (this.sum == 1) {
        // 区域完成，设置sum=-1表示打点结束
        this.sum = -1;
        // 将区域边框去掉
        this.$refs.ul.style.display = "none";
        this.areas[this.areas.length - 1].style =
          "fill:lime;stroke:rgb(128, 73, 0);stroke-width:0";
      }
      // }, 200);
    },
    // 添加一个区域
    addPolygonArea(x, y) {
      if (this.sum == 0) {
        let obj = {
          label: "",
          picUrl: [],
          points: `${x},${y}`,
          properties: {}
        };
        this.cx = x;
        this.cy = y;
        this.isshowBPlo = true;
        // wwcattle now  
        this.activeIndex = this.areasDatas.length;
        this.areasDatas.push(obj);
        // 打点中
        this.sum = 1;
        this.activeArea = obj;
      } else if (this.sum == 1) {
        this.activeArea.points = this.activeArea.points + ` ${x},${y}`;
        this.areasDatas[this.activeIndex] = this.activeArea
      }
    },

    // 点击开始区域
    addAreaBtns() {
      // 如果有选中点焦点，取消，并显示区域 侧边栏
      this.isActive = -1;
      console.log(this.areasDatas.length);
      this.$emit("clickDetail", {
        // 设置一个属性 辨认是回流区域数据
        indexState: this.areasDatas.length,
        isArea: true,
        ...this.areasDatas[this.areasDatas.length],
        isopenDel: true,
      });

      // 用户已经右键  再点击右键
      if (this.sum == 1) return;

      // 设置随机区域颜色
      let ram = this.randomColors();
      this.hasAreasColors.push(ram);
      console.log(ram);
      // sum 0,1,2表区域打点的开始，打点中，打点结束
      this.sum = 0;
      this.addPolygonArea(this.offsetX, this.offsetY);
      this.$refs.ul.style.display = "none";
    },

    // 区域过程中
    svgclick(e) {
      console.log(this.sum);
      if (this.sum == 1) {
        this.addPolygonArea(e.offsetX, e.offsetY);
      }
    },

    // 区域撤销上一步
    areaBackBtns() {
      if (this.sum == 1) {
        let f = this.activeArea.points;
        let arr = f.split(" ");
        if (arr.length == 2) return;
        arr.pop();
        // if (arr.length == 0) return this.deleteAreaBtn();
        let str = arr.join(" ");
        this.activeArea.points = str;
        this.areasDatas[this.activeIndex] = this.activeArea
        this.$refs.ul.style.display = "none";
      }
    },

    // 删除区域
    deleteAreaBtn() {
      this.isshowBPlo = false;
      // this.deleteIndexTotal = this.areas.length;
      // console.log(th);
      //删除 当前区域与区域所对应的备注
      this.areasDatas.splice(this.activeIndex, 1);
      this.hasAreasColors.splice(this.activeIndex, 1);
      // this.areas[this.activeIndex].points="";
      // let index = this.addTextDesArr.findIndex(
      //   (val) => val.stateIndex == this.activeIndex
      // );
      this.addTextDesArr.splice(this.activeIndex, 1);

      // 设置当前没有选中的区域
      this.activeIndex = null;
      // 区域结束
      this.sum = -1;
    },

    // 点击区域成功！
    addAreaSucss() {
      // 影藏区域起始点
      this.isshowBPlo = false;
      if (this.sum == 1) {
        // 区域完成，设置sum=-1表示打点结束
        this.sum = -1;
        // 将区域边框去掉
        this.$refs.ul.style.display = "none";
        // 区域占位，该区域，之后删除会把改区域，文本，颜色一起同时删除
        let gg = this.addTextDesArr[this.activeIndex];
        if (!gg) {
          this.addTextDesArr.splice(this.activeIndex, 1, {
            stateIndex: this.activeIndex,
            x: 0,
            y: 0,
            value: "",
          });
        }
        // this.activeArea.style =
        this.activeIndex = null;
        //   "fill:lime;stroke:rgb(128, 73, 0);stroke-width:0";
        // let f = this.paylon.attributes.points.nodeValue;
        // var arr = f.split(" ");
        // console.log(arr);
        // arr = arr.map((val) => {
        //   var f = val.split(",");
        //   return [f[0], f[1]];
        // });
        // let g = Object.assign(this.paylon);
        // let obj = new Map();
        // obj.set(g, arr);
        // this.addAreas.push(obj);
      }


      // 右边栏数据为空 
      this.$emit("clickDetail", {
        width: 0,
        height: 0,
        left: 0,
        top: 0,
        isopenDel: false,
      });
    },

    // 删除打点
    deleteModelBtn() {
      if (!(this.isActive >= 0)) return;
      console.log("----------");
      console.log(this.isActive);
      // 确定当前有选中dosomething........
      this.dottedDatas.splice(this.isActive, 1);
      // 右边区域size应该重置为初始化无
      this.$emit("clickDetail", {
        width: 0,
        height: 0,
        left: 0,
        top: 0,
        isopenDel: false,
      });

      this.isActive = -1;

      // contextMenu隐藏
      this.$refs.ul.style.display = "none";
    },

    // 添加打点
    addModelBtn(e) {
      // wwcattle 
      this.dialogVisible = true;
      // let str = `  
      // <el-select size="small" v-model="value" placeholder="请选择">
      //     <el-option
      //       v-for="item in options"
      //       :key="item.value"
      //       :label="item.label"
      //       :value="item.value"
      //     ></el-option>
      //   </el-select> `
      // let g = "<span>{{dotNames}}</span>"
      // this.$alert(str, 'HTML 片段', {
      //   dangerouslyUseHTMLString: true
      // });
      // this.$prompt("请选择标记备注", "提示", {
      //   confirmButtonText: "确定",
      //   cancelButtonText: "取消",
      // })
      //   .then(({ value }) => {
      //     // 添加一个新的打点记录
      // this.dottedDatas.push({
      //   left: this.offsetX,
      //   top: this.offsetY,
      //   label: "画面20",
      //   picUrl: [],
      // });
      //     this.isActive = this.dottedDatas.length - 1;
      //     this.$emit("clickDetail", {
      //       ...this.dottedDatas[this.dottedDatas.length - 1],
      //       isopenDel: true,
      //     });
      //     this.$refs.ul.style.display = "none";
      //   })
      //   .catch(() => {
      //     // this.$message({
      //     //   type: "info",
      //     //   message: "取消输入",
      //     // });
      //   });

    },

    // 影藏oncontextMenu
    parengClickBtn(e) {
      //  adudge如果点击之外隐藏标记与删除盒子contextMenu
      // if (e.target.tagName != "LI") {
      // 点击之后把所有的右键菜单给影藏
      this.$refs.ul.style.display = "none";
      this.$refs.areas.style.display = "none";

      // }
    },
    // 图片加载完进行将图片撑高的高度发送到父盒子中，之后整体进行居中
    imgload() {
      this.nowWidth = this.$refs.imgRef.offsetWidth;
      this.nowHeight = this.$refs.imgRef.offsetHeight;
      // 图片加载完 子盒子在父盒子中居中布局
      this.$emit("sendIntSizeRange", {
        longModelW: this.longDottedSizeW,
        shortModelW: this.shortDottedSizeW,
        nowWidth: this.nowWidth,
        nowHeight: this.nowHeight,
      });
    },
    // 右键菜单组件
    contextMenuBtn(e) {
      // 保证只有一个右键菜单组件的出现
      //在svg中进行右键的时候。出现添加标记
      // 在paylon中右键出先删除与提那家备注
      if (e.target.nodeName != "polygon") {
        this.$refs.areas.style.display = "none";
        this.$refs.ul.style.display = "block";
        e.preventDefault();
        this.$refs.ul.style.left = e.clientX + "px";
        this.$refs.ul.style.top = e.clientY + "px";
        let { offsetX, offsetY } = e;
        this.offsetX = offsetX;
        this.offsetY = offsetY;
      }
    },

    ondreopStop(indexs) {
      this.addGraCoordinate.forEach((val, index) => {
        if (indexs == index) {
          this.addGraCoordinate[index] = this.newRect;
        }
        return;
      });
    },
    clickModelBtn(index) {
      // console.log(index);
      if(this.isActive==index)return;
      // if(this.activeIndex<0)return;
      // 记录 当前点击的打点model-Index
      this.isActive = index;
      this.$emit("clickDetail", {
        indexState: index,
        ...this.dottedDatas[index],
        isopenDel: true,
      });
    },
    mouseUpBtn(e) {
      console.log(e);
    },
    mouseDownBtn(e) {
      let dom = this.$refs.parentRef;
      // 点击之后记录的值
      let { clientX: x, clientY: y } = e;
      let boxPosLeft = dom.offsetLeft;
      let boxPosTop = dom.offsetTop;
      document.onmousemove = function(e) {
        // 移动的值得坐标
        let { clientX, clientY } = e;
        let changeX = clientX - x;
        let changeY = clientY - y;
        dom.style.left = changeX + boxPosLeft + "px";
        dom.style.top = changeY + boxPosTop + "px";
        return false;
      };
      document.onmouseup = function() {
        // 设置必须点击拖动才可以移动拖拽
        document.onmousemove = null;
        return false;
      };
      // 必须添加
      e.preventDefault();
      return false;
    },
    judgSize() { },
    resize(newRect) {
      // console.log(newRect);
      // this.newRect = newRect;
      // this.$emit("clickDetail", {
      //   ...newRect,
      //   isopenDel: true,
      // });
      // return newRect;
    },
  },
  watch: {
    // dotNames: {
    //   handler(newVal) {
    //     console.log(newVal+"Watch");
    //   },
    //   immediate: true
    // },
    // dottedDetail: {
    //   handler(newVal) {
    //     // this.dottedDetails=JSON.parse(JSON.string)
    //     this.dottedDetails = JSON.parse(JSON.stringify(newVal))
    //     // console.log(newVal);
    //   },
    //   immediate: true
    // },
    sum(newVal) {
      if (newVal == 1) {
        let that = this;
        window.onkeypress = function(event) {
          if (event.ctrlKey && event.keyCode == 26) {
            that.areaBackBtns();
          }
          // event.preventDefault();
          // return false;
        };
      }
    },
    // 放大或者缩小
    stateTag(newVal) {
      // console.log(newVal);
      // this.$refs.parentRef.style.transform="scale(1.5)"
      let beforeWidth = Number(
        getComputedStyle(this.$refs.parentRef).width.split("p")[0]
      );
      let beforeHeihgt = Number(
        getComputedStyle(this.$refs.parentRef).height.split("p")[0]
      );
      // 判断点击放大还是缩小，按70px进行变化
      let nowWidth =
        this.stateStr == "expand" ? beforeWidth + 70 : beforeWidth - 70;
      let nowHeight = (nowWidth * beforeHeihgt) / beforeWidth.toFixed(2);
      // 放大 缩小的限制范围
      if (nowWidth <= this.shortDottedSizeW || nowWidth > this.longDottedSizeW)
        return;
      this.$refs.parentRef.style.width = nowWidth + "px";
      this.$refs.parentRef.style.height = nowHeight + "px";
      this.nowHeight = nowHeight;
      this.nowWidth = nowWidth;
      // 发送给外部父盒子的大小，之后父盒子再对整体子盒子居中
      this.$emit("adudge", {
        longModelW: this.longDottedSizeW,
        shortModelW: this.shortDottedSizeW,
        nowWidth: this.nowWidth,
        nowHeight: this.nowHeight,
      });
      this.$nextTick(() => {
        // 对里面的坐标进行等比例大小变化
        if (this.dottedDatas.length) {
          this.dottedDatas.forEach((val) => {
            let { left, top } = val;
            val.left =Number(Number( (left * nowWidth) / beforeWidth).toFixed(4));
            val.top = Number(Number((top * nowHeight) / beforeHeihgt).toFixed(4));
          });
        }
        // 对里面的区域等比例放大或缩小
        if (this.areasDatas.length) {
          this.areasDatas.forEach((val) => {
            let arr = val.points.split(" ");
            val.points = arr
              .map((val) => {
                let str = val.split(",");
                var left = ((str[0] * nowWidth) / beforeWidth).toFixed(2);
                var top = ((str[1] * nowHeight) / beforeHeihgt).toFixed(2);
                return `${left},${top}`;
              })
              .join(" ");
          });
        }

        // 对区域里面的备注位置进行等比例移动
        if (this.addTextDesArr.length) {
          this.addTextDesArr.forEach((val) => {
            val["x"] = ((val["x"] * nowWidth) / beforeWidth).toFixed(2);
            val["y"] = ((val["y"] * nowHeight) / beforeHeihgt).toFixed(2);
            // return { x, y ,value:val["value"]};
          });
        }

        // 对有可能发生打点时，就放大缩小
        if (this.cx && this.cy) {
          this.cx = ((this.cx * nowWidth) / beforeWidth).toFixed(2);
          this.cy = ((this.cy * nowHeight) / beforeHeihgt).toFixed(2);
        }

        // if (this.addTextDesArr.length) {
        //   this.addTextDesArr = this.addTextDesArr.map((val) => {
        //     var x = ((val["x"] * nowWidth) / beforeWidth).toFixed(2);
        //     var y = ((val["y"] * nowHeight) / beforeHeihgt).toFixed(2);
        //     return { x, y ,value:val["value"]};
        //   });
        // }
      });
    },
    nowWidth(newVal) {
      // let { addGraCoordinate, nowWidth, nowHeight } = this.$data;
      // this.$emit("modelTagsDetailData", {
      //   areaDetail: {
      //     areas: this.areas,
      //     hasAreasColors: this.hasAreasColors,
      //     addTextDesArr: this.addTextDesArr,
      //   },
      //   addGraCoordinate,
      //   nowHeight: Number(nowHeight.toFixed(2)),
      //   nowWidth,
      //   imgSrc: this.backImg,
      // });
    },

    // immediate: true,
  },
  beforeDestroy() {
    eventbus.$off();
  }
};
</script>

